export default {
	data() {
		return {
			amount: '',
			Re_price_arr: ['198', '500', '1000', '3000', '5000', '10000', '20000', '30000', '49998'],
			uid: 0,
			type: '' ,//商品或金额渠道
			firm_id: ''
		}
	},
	onLoad(payload) {
		this.firm_id = payload.firm_id
		console.log(this.firm_id)
	},
	methods: {
		//发起支付
		btnRecharge() {
			let baseUrl = this.WebUrl
			let that = this
			if (Math.floor(that.amount) !== that.amount * 1) {
				uni.showModal({
					title: '请填写整数',
					showCancel: false
				})
			}else {
				uni.getStorage({
					key: 'openid',
					success(res) {
						console.log(res)
						uni.getStorage({
							key: 'u_id',
							success(resId) {
								console.log(resId)
								uni.request({
									url: baseUrl + '/wxPay',
									method: 'POST',
									data: {
										openid: res.data,
										money: that.amount,
										uid: resId.data,
										firmid: that.firm_id
									},
									success(res) {
										wx.requestPayment({
											timeStamp: res.data.timeStamp,
											nonceStr: res.data.nonceStr,
											package: res.data.package,
											signType: res.data.signType,
											paySign: res.data.paySign,
											success(resPay) {
												if(resPay.errMsg === "requestPayment:ok"){
													uni.showModal({
														title: '充值成功',
														showCancel:false,
														success(res) {
															if(res.confirm) {
																uni.navigateBack({
																	delta: 1
																})
															}
														}
													})
												}
											}
										})
									}
								})
							}
						})
					}
				})
			}
		},

		selectPrice(item) {
			this.amount = item
		},
	}
}
